$('document').ready(() => {
    // troca a background color da navbar ao clicar no botÃ£o de expandir
    $('#hamburger-menu').click(() => {
        setTimeout(() => {
            var height = $('nav').height()
            if (height > 150) {
                $('nav').css('background-color', '#0b5be4')
            } else {
                $('nav').css('background-color', '')
            }
        }, 200);
    })

    //  troca a cor da navbar ao descer
    $(window).scroll(() => {
        $('nav').toggleClass('nav-after-scroll animated fadeInDown', $(this).scrollTop() > $('nav').height())

    })

    //
    //
    //
    //
    // OWL TROCAR PARA OUTRO JS DEPPOIS
    $('.owl-carousel').owlCarousel({
        loop: true,
        margin: 10,

        responsive: {
            0: {
                items: 1
            },
            600: {
                items: 3
            },
            1000: {
                items: 5
            }
        }
    })


    //
    //
    //
    //
    // PARALLAX TROCAR PARA OUTRO JS DEPOIS
    $(window).scroll(function () {
        var top_of_element = $(".parallax").offset().top;
        var bottom_of_element = $(".parallax").offset().top + $(".parallax").outerHeight();
        var bottom_of_screen = $(window).scrollTop() + $(window).innerHeight();
        var top_of_screen = $(window).scrollTop();

        if ((bottom_of_screen > top_of_element) && (top_of_screen < bottom_of_element)) {
            $("#parallax-content").addClass('animated bounceInUp')
        } else {
            $("#parallax-content").removeClass('animated bounceInUp')
        }
    });





})